@import url(http://fonts.googleapis.com/earlyaccess/droidarabickufi.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div#headerContainer {
  direction: rtl;
}
img#logo {
  border-style: solid;
  border-width: 0px;
  border-color: black;
  float: right;
  width: 90px;
  height: 90px;
  margin-top: 5px;
  margin-right: 15px;
}

div#NavBarContainer {
  /* font-family: Arial, Helvetica, sans-serif; */
  padding-top: 40px;
  background-color: #333;
  padding-right: 0px;
}

div#bodyContainer {
  width: 60%;
  min-height: 400px;

  border-style: solid;
  border-width: 3px;
  border-color: #9876;
  margin-left: 20%;
  align-content: center;
  justify-content: table;

  /* font-family: Verdana, Geneva, Tahoma, sans-serif; */

  /* display: flex; */
  align-items: center;
  direction: rtl;
  /* background-image: url("./imgs/logo.jpeg");
  opacity: 0.05; */
  background-color: #ffffff; /* Used if the image is unavailable */
  /* height: 500px; You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;

  

}
div#imageSliderContainer{
width: 100%;
/* height: 400px; */
}
img#bbg {
  background-image: url(/racing/static/media/logo.21df5285.jpeg);
  border-style: solid;
  border-width: 0px;
  border-color: black;
  float: right;
  width: 90px;
  height: 90px;
  margin-top: 5px;
  margin-right: 15px;
}
div#raceTitleBoxContainer{
  width:100%;
float:left;
background:#ccc;
align-items: center;
display: flex;
}

div#raceTitleBox{
  width: 90%;
  height: 100px;
  margin-right: auto;
  margin-left: auto;
  /* margin-top: 10px; */
  border-style: solid;
  border-width: 1px;
  border-color: #444;
  text-align: justify;
  color: #567
  
}
div#footerContainer {
  
  border-style: solid;
  border-width: 0px;
  border-color:#9876;
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  direction: rtl;
  background-color: #333;
}
div#footerContent{
  width: 80%;
  margin-right: 10%;
  display: flex;
}
div#footerBox{
  width: 30%;
  height: 200px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 17px;
  border-style: solid;
  border-width: 1px;
  border-color: #4441;
  text-align: justify;
  color: #ffffff55;
  font-size: 15px;
}
div#copyRight{
  width: 100%;
  height: 20px;
  background-color: #7be2af
}
div#line{
  width: 100%;
  height: 10px;
  background-color: #222
}



.droid-arabic-kufi {
  font-family: 'Droid Arabic Kufi', serif;
}
